import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { formatTimeRange } from "../../utils/common";
import { fetchEvent } from "../../helper/api";

const EventPopup = ({ event, eRef, onClose }) => {
  const [eventData, setEventData] = useState(null);
  const [userData, setUserData] = useState(null);

  useEffect(() => {
    getEventDetails(event.id);
  }, [event.id]);

  const getEventDetails = async (id) => {
    try {
      const URL = "calendar_app/api/calendar_meeting";
      const event = await fetchEvent(URL, id);
      setEventData(event);
      setUserData(event.user_det);
    } catch (error) {
      console.error("Error loading event:", error);
    }
  };

  return (
    <div className="custom-popup" onClick={onClose} ref={eRef}>
      <div className="popup-content" onClick={(e) => e.stopPropagation()}>
        {!eventData || !userData ? (
          <p className="cus-p">Event Details Loading...</p>
        ) : (
          <>
            <div className="popup-left">
              <p>Interview With: {userData.candidate.candidate_firstName}</p>
              <p>Position: {userData.job_id.jobRequest_Title}</p>
              <p>Created By: {userData.handled_by.firstName}</p>
              <p>
                Interview Date: {new Date(eventData.start).toLocaleDateString()}
              </p>
              <p>
                Interview Time:{" "}
                {formatTimeRange(eventData.start, eventData.end)}
              </p>
              <p>Interview Via: Google Meet</p>
              <div className="cus-btn">
                <span className="text">Resume.docx</span>
                <span className="icons">
                  <i className="fa-regular fa-eye"></i>
                  <i className="fa-solid fa-download"></i>
                </span>
              </div>
              <div className="cus-btn">
                <span className="text">Adharcard</span>
                <span className="icons">
                  <i className="fa-regular fa-eye"></i>
                  <i className="fa-solid fa-download"></i>
                </span>
              </div>
            </div>
            <div className="popup-right">
              <img
                src="https://www.brandeis.edu/its/_files/google-meet-logo.png"
                className="meet-icon"
                alt="Google Meet"
              />
              <Link target="_blank" to={eventData.link}>
                <button className="join-button">JOIN</button>
              </Link>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default EventPopup;
